import React, { useEffect, useState } from "react";
import { Button, Card, Pagination } from "react-bootstrap";
import IMAGES from "../../../assets/images";
import { FaFilePrescription } from "react-icons/fa6";
import { useNavigate } from "react-router-dom";
import { PATH } from "../../../utils/path";
import { useDispatch, useSelector } from "react-redux";
import { allPatientPlanData } from "../../../app/features/subscriptionPlans/subscriptionPlans.slice";
import FullScreenLoader from "../../../authorization/FullScreenLoader";
import { remainingAptPres } from "../../../app/features/doctorSchedule/doctorSchedule.slice";
import SubscriptionPlanModal from "./PurchasePlans/SubscriptionPlanModal";

export default function MyPlans() {
  const [currentPage, setCurrentPage] = useState(1);

  const navigate = useNavigate();
  const dispatch = useDispatch();
  const [showMessage, setShowMessage] = useState(false);

  const { getAllPatientPlanList, isLoading, isSuccess, isError } = useSelector(
    (state) => state?.subscriptionPlans
  );
  const user = JSON.parse(localStorage.getItem("family_doc_app"));

  const cardsPerPage = 12;

  const indexOfLastCard = currentPage * cardsPerPage;
  const indexOfFirstCard = indexOfLastCard - cardsPerPage;
  const currentCards =
    getAllPatientPlanList && getAllPatientPlanList.length
      ? getAllPatientPlanList.slice(
          indexOfFirstCard,
          Math.min(indexOfLastCard, getAllPatientPlanList.length)
        )
      : [];

  const paginate = (pageNumber) => setCurrentPage(pageNumber);
  const customTotal = (from, to, size) => (
    <span>
      {from} to {to} out of {size} entries
    </span>
  );
  useEffect(() => {
    const finalData = {
      patientId: user?.userId,
      IsPagination: true,
      PageNo: currentPage,
      Size: 5,
    };
    dispatch(allPatientPlanData(finalData));
  }, [dispatch, user?.userId, currentPage]);

  const { remainingAptPresData } = useSelector(
    (state) => state?.doctorSchedule
  );

  const handleSubmit = () => {
    if (
      remainingAptPresData?.data?.appointment &&
      remainingAptPresData?.data?.prescription
    ) {
      setShowMessage(true);
    } else {
      navigate(PATH.PATIENT_PURCHASE_PLANS);
    }
  };

  useEffect(() => {
    const finalData = {
      patientId: user?.userId,
    };
    dispatch(remainingAptPres(finalData));
  }, [dispatch, user?.userId]);

  return (
    <>
      <h5>My Plans</h5>
      <Card className="shadow-sm mt-4 p-3">
        { isLoading ? (
          <FullScreenLoader />
        ) : isSuccess ? (
          <>
            <div className="d-flex justify-content-end">
              <Button
                variant="primary"
                type="button"
                className="d-flex Admin-Add-btn fw-bold"
                onClick={handleSubmit}
              >
                {getAllPatientPlanList?.length > 0
                  ? "Purchase Another Plan"
                  : "Purchase Plan"}
              </Button>
            </div>
            <div>
              {getAllPatientPlanList?.length > 0 ? (
                <div className="my-plans patient__prescription-forms my-3">
                  {currentCards.map((card, index) => (
                    <div className="container" key={index}>
                      <h4 className="fw-bold m-0">{card?.plan}</h4>
                      <div className="d-flex align-items-end justify-content-center my-4">
                        <h2 className="fw-bold m-0 me-2">€{card?.amount}</h2>
                        <span className="day-left-status px-3">
                          {card?.remainingPlanDays}
                        </span>
                      </div>
                      <div className="patient__subscription-plan">
                        <div className="plans-count">
                          <img
                            src={IMAGES.APPOINTMENT_ICON}
                            alt="appointment icon"
                            className="color-dk-blue mb-2"
                            width={30}
                          />
                          <h6 className="m-0">Appointments</h6>
                          <h5 className="fw-bold m-0">
                            {card?.remaingAppointments}
                          </h5>
                        </div>
                        <div className="plans-count">
                          <FaFilePrescription
                            className="color-dk-blue mb-2"
                            size={32}
                          />
                          <h6 className="m-0">Prescriptions</h6>
                          <h5 className="fw-bold m-0">
                            {card?.remaingPrescription}
                          </h5>
                        </div>
                      </div>
                      <div className="ribbon-wrap">
                        <div
                          className={`ribbon ${
                            card?.isActive ? "active-status" : "inactive-status"
                          }`}
                        >
                          {card?.isActive ? "Active" : "InActive"}
                        </div>
                      </div>
                    </div>
                  ))}
                </div>
              ) : (
                <div
                  className="d-flex justify-content-center flex-column align-items-center"
                  style={{ minHeight: "32rem" }}
                >
                  <img
                    src={IMAGES.PURCHASE_PLANS}
                    alt="purchase plans line"
                    width={97}
                  />
                  <p className="text-center">
                    You don&apos;t have any purchased Plan Please click on{" "}
                    <span className="fw-bold">Purchase Plan</span>
                  </p>
                </div>
              )}
            </div>
            {getAllPatientPlanList.length > 0 && (
              <Pagination className="d-flex justify-content-between align-items-center px-3 patient__prescription-pagination">
                <Pagination>
                  {customTotal(
                    indexOfFirstCard + 1,
                    indexOfLastCard,
                    getAllPatientPlanList.length
                  )}
                </Pagination>
                <div className="d-flex justify-content-between pagination__page-Number">
                  <Pagination.Prev
                    onClick={() => paginate(currentPage - 1)}
                    disabled={currentPage === 1}
                  />
                  {Array.from({
                    length: Math.ceil(
                      getAllPatientPlanList.length / cardsPerPage
                    ),
                  }).map((_, index) => (
                    <Pagination.Item
                      key={index + 1}
                      active={currentPage === index + 1}
                      onClick={() => paginate(index + 1)}
                    >
                      {index + 1}
                    </Pagination.Item>
                  ))}
                  <Pagination.Next
                    onClick={() => paginate(currentPage + 1)}
                    disabled={
                      currentPage ===
                      Math.ceil(getAllPatientPlanList.length / cardsPerPage)
                    }
                  />
                </div>
              </Pagination>
            )}
          </>
        ) : isError ? (
          <span className="text-danger fst-italic">Network Error</span>
        ) : null}
      </Card>
      <SubscriptionPlanModal
        handleClose={() => setShowMessage(false)}
        show={showMessage}
      />
    </>
  );
}
